<template>
    <div class="coldiv">
		<img src="@/assets/common/subtitleYellow.png" class="bgi" alt="">
		<img src="@/assets/common/subtitleIcon.png" class="subtitleIcon" alt="">
		<span class="specialFont">{{specialFont}}</span>
		<span class="subtitleText">{{content}}</span>
		<span class="remark" v-if="remarkText">{{remarkText}}</span>
	</div>
</template>

<style lang="less" scoped>
.coldiv {
	width: 100%;
	height: 3.7vh;
	position: relative;
	display: flex;
	align-items: center;
	
	
	.bgi {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
	}
	
	.subtitleIcon {
		height: 100%;
		aspect-ratio: 1/1;
		margin-left: 2.86%;
	}
	.specialFont {
		height: 4.16vh;
		line-height: 4.16vh;
		font-size: 2.77vh;
		font-family: 'yuweij', sans-serif;
		color: #fff;
		text-shadow: 0px 0px 2.14% rgba(255,208,59,0.7);
		font-weight: 500;
		margin-left: 0.95%;
	}
	.subtitleText {
		height: 2.78vh;
		line-height: 2.78vh;
		font-size: 1.85vh;
		font-family: 'titleFont', sans-serif;
		color: #fff;
		text-shadow: 0px 0px 2.14% rgba(255,208,59,0.7);
		font-weight: 400;
	}
	.remark {
		margin-left: 41.67%;
		height: 60%;
		line-height: 200%;
		font-size: 83.33%;
		font-family: 'titleFont', sans-serif;
		color: #fff;
		font-weight: 400;
	}
}
</style>

<script>
export default {
	props:{
		text:{
			type: String
		},
		remark:{
			type: String
		},
		special:{
			type: String
		}
	},
	data(){
		return {
			content: '',
			remarkText: '',
			specialFont: ''
		}
	},
	mounted(){
		this.content = this.text;
		if(this.remark){
			this.remarkText = this.remark;
		}
		if(this.special){
			this.specialFont = this.special;
		}
		return false;
	}
}
</script>

